import { h, Fragment } from 'preact'
import { 
  Container, 
  VerticalSpace, 
  Text,
  Button,
  LoadingIndicator
} from '@create-figma-plugin/ui'
import { emit, on, EventHandler } from '@create-figma-plugin/utilities'
import { useState, useEffect } from 'preact/hooks'

import { ComponentInfo as ComponentInfoType, SynonymGroup, MessageToUI } from '../types'
import { ComponentInfo } from './ComponentInfo'
import { SynonymGroups } from './SynonymGroups'

export function App() {
  const [componentInfo, setComponentInfo] = useState<ComponentInfoType>({
    name: 'Select a component',
    type: '',
    description: 'No component selected',
    hasDescription: false
  })
  const [synonymGroups, setSynonymGroups] = useState<SynonymGroup[]>([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const handlers: { name: string; handler: EventHandler }[] = [
      {
        name: 'SELECTION_CHANGE',
        handler: (message: MessageToUI) => {
          if (message.type === 'SELECTION_CHANGE') {
            setComponentInfo(message.component)
            setSynonymGroups([])
            setError(null)
          }
        }
      },
      {
        name: 'SYNONYMS_GENERATED',
        handler: (message: MessageToUI) => {
          if (message.type === 'SYNONYMS_GENERATED') {
            setSynonymGroups(message.groups)
            setLoading(false)
            setError(null)
          }
        }
      },
      {
        name: 'GENERATE_ERROR',
        handler: (message: MessageToUI) => {
          if (message.type === 'GENERATE_ERROR') {
            setError(message.error)
            setLoading(false)
          }
        }
      }
    ]

    handlers.forEach(({ name, handler }) => {
      on(name, handler)
    })

    return () => {
      handlers.forEach(({ name, handler }) => {
        // Clean up event listeners
        // Note: off function might not be available in current version
        // of @create-figma-plugin/utilities
      })
    }
  }, [])

  const handleGenerate = () => {
    setLoading(true)
    setError(null)
    emit('GENERATE_SYNONYMS')
  }

  return (
    <Container space="medium">
      <VerticalSpace space="large" />
      
      {/* Component Information */}
      <ComponentInfo {...componentInfo} />
      
      <VerticalSpace space="medium" />
      
      {/* Generate Button */}
      <Button 
        fullWidth 
        onClick={handleGenerate}
        disabled={loading || !componentInfo.name || componentInfo.name === 'Select a component'}
      >
        {loading ? 'Generating...' : 'Generate Synonyms'}
      </Button>
      
      {error && (
        <>
          <VerticalSpace space="small" />
          <Text style={{ color: 'var(--figma-color-text-danger)' }}>{error}</Text>
        </>
      )}
      
      <VerticalSpace space="medium" />
      
      {/* Synonym Groups */}
      <SynonymGroups 
        groups={synonymGroups}
        loading={loading}
      />
      
      <VerticalSpace space="medium" />
    </Container>
  )
} 